import { Xt } from 'xtendui'
import 'xtendui/src/core/drop'
import gsap from 'gsap'

Xt.mount.push({
  matches: '.demo--drop-animation-js-multiple',
  mount: ({ object }) => {
    // vars

    const targetTimeOn = 0.5
    const targetEaseOn = 'quint.out'
    const targetTimeOff = 0.5
    const targetEaseOff = 'quint.out'

    // init

    let self = new Xt.Drop(object, {
      elements: ':scope > a, :scope > button',
      targets: ':scope > .xt-drop',
      duration: 500,
    })

    // on

    const eventOn = e => {
      const tr = e.target.querySelector(':scope > *')
      gsap.killTweensOf(tr)
      gsap.set(tr, {
        x: -self.direction * 15,
        opacity: 0,
      })
      gsap.to(tr, {
        x: 0,
        opacity: 1,
        duration: targetTimeOn,
        ease: targetEaseOn,
      })
    }

    for (const target of self.targets) {
      target.addEventListener('on.xt.drop', eventOn)
    }

    // off

    const eventOff = e => {
      const tr = e.target.querySelector(':scope > *')
      gsap.killTweensOf(tr)
      gsap.to(tr, {
        x: self.direction * 15,
        opacity: 0,
        duration: targetTimeOff,
        ease: targetEaseOff,
      })
    }

    for (const target of self.targets) {
      target.addEventListener('off.xt.drop', eventOff)
    }

    // unmount

    return () => {
      self.destroy()
      self = null
    }
  },
})
